import React from "react"
import { HashRouter, Routes, Route } from "react-router-dom"
import Jobwork from "./components/Jobwork"
import Landing from "./components/Landing"
import Making from "./components/Making"
import TaxInvoice from "./components/TaxInvoice"

export default function App() {

  return (
      <HashRouter>
          <Route
            path="/bsm-invoice"
            element={
              <React.Fragment>
                <Landing />
              </React.Fragment>
            }
          ></Route>
          <Route
            path="/jobwork"
            element={
              <React.Fragment>
                <Jobwork />
              </React.Fragment>
            }
          ></Route>

          <Route
            path="/making"
            element={
              <React.Fragment>
                <Making />
              </React.Fragment>
            }
          ></Route>

          <Route
            path="/taxinvoice"
            element={
              <React.Fragment>
                <TaxInvoice />
              </React.Fragment>
            }
          ></Route>
      </HashRouter>
  )
}
