import React, { useState, useEffect } from "react"
import { AiOutlineDelete, AiOutlineEdit } from "react-icons/ai"
import { v4 as uuidv4 } from "uuid"
import { toast, ToastContainer } from "react-toastify"
import "react-toastify/dist/ReactToastify.css"

export default function TaxTable({
  description,
  setDescription,
  hsn,
  setHsn,
  gst,
  setGst,
  pcs,
  setPcs,
  gweight,
  setGweight,
  nweight,
  setNweight,
  rate,
  setRate,
  amount,
  setAmount,
  list,
  setList,
  total,
  setTotal,
}) {
  const [isEditing, setIsEditing] = useState(false)

  // Submit form function
  const handleSubmit = (e) => {
    e.preventDefault()

    if (!description || !hsn || !gst || !pcs || !nweight || !gweight || !rate) {
      toast.error("Please fill in all inputs")
    } else {
      const newItems = {
        id: uuidv4(),
        description,
        hsn,
        gst,
        pcs,
        gweight,
        nweight,
        rate,
        amount,
      }
      setDescription("")
      setHsn(0)
      setGst(0)
      setPcs(0)
      setGweight(0)
      setNweight(0)
      setRate(0)
      setAmount("")
      setList([...list, newItems])
      setIsEditing(false)
    }
  }

  // Calculate items amount function
  useEffect(() => {
    const calculateAmount = (amount) => {
     
      let total = parseFloat(rate)
      setAmount(total.toFixed(3))
    }

    calculateAmount(amount)
  }, [amount, rate, setAmount])

  // Calculate total amount of items in table
  useEffect(() => {
    let rows = document.querySelectorAll(".amount")
    let sum = 0

    for (let i = 0; i < rows.length; i++) {
      if (rows[i].className === "amount") {
        sum += isNaN(rows[i].innerHTML) ? 0 : parseFloat(rows[i].innerHTML)
        setTotal(sum.toFixed(3))
      }
    }
  })

  // Edit function
  const editRow = (id) => {
    const editingRow = list.find((row) => row.id === id)
    setList(list.filter((row) => row.id !== id))
    setIsEditing(true)
    setDescription(editingRow.description)
    setHsn(editingRow.hsn)
    setGst(editingRow.gst)
    setPcs(editingRow.pcs)
    setGweight(editingRow.gweight)
    setNweight(editingRow.nweight)
    setRate(editingRow.rate)
  }

  // Delete function
  const deleteRow = (id) => setList(list.filter((row) => row.id !== id))

  return (
    <>
      <ToastContainer position="top-right" theme="colored" />

      <form onSubmit={handleSubmit}>
        <div className="flex flex-col md:mt-16">
          <label htmlFor="description">Item description</label>
          
          <select
            id="description"
            name="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          >
              <option>select description</option>
              <option value="NEW GOLD ORNAMENTS 22CT">NEW GOLD ORNAMENTS 22CT</option>
              <option value="NEW GOLD ORNAMENTS 24CT">NEW GOLD ORNAMENTS 24CT</option>
              <option value="pending">Pending</option>
              <option value="denied">Denied</option>
          </select>
        </div>

        <div className="md:grid grid-cols-4 gap-10">
          <div className="flex flex-col">
            <label htmlFor="hsn">HSN/SAC</label>
            <input
              type="number"
              name="Hsn"
              id="hsn"
              placeholder="Hsn"
              value={hsn}
              onChange={(e) => setHsn(e.target.value)}
            />
          </div>

          <div className="flex flex-col">
            <label htmlFor="stone">GST</label>
            <input
              type="number"
              name="Gst"
              id="gst"
              placeholder="Gst"
              value={gst}
              onChange={(e) => setGst(e.target.value)}
            />
          </div>
          <div className="flex flex-col">
            <label htmlFor="Wastage">PCS</label>
            <input
            type="number"
              name="Pcs"
              id="Pcs"
              placeholder="Pcs"
              value={pcs}
              onChange={(e) => setPcs(e.target.value)}
            />
          </div>
          <div className="flex flex-col">
            <label htmlFor="Wastage">Gross Wt</label>
            <input
            type="number"
              name="Gweight"
              id="Gweight"
              placeholder="Gweight"
              value={gweight}
              onChange={(e) => setGweight(e.target.value)}
            />
          </div>
          
        </div>
        <div  className="md:grid grid-cols-3 gap-10">
        <div className="flex flex-col">
            <label htmlFor="Wastage">Net Wt</label>
            <input
            type="number"
              name="Nweight"
              id="Nweight"
              placeholder="Nweight"
              value={nweight}
              onChange={(e) => setNweight(e.target.value)}
            />
          </div>
          <div className="flex flex-col">
            <label htmlFor="Wastage">Rate</label>
            <input
            type="number"
              name="Rate"
              id="Rate"
              placeholder="Rate"
              value={rate}
              onChange={(e) => setRate(e.target.value)}
            />
          </div>
        <div className="flex flex-col">
            <label htmlFor="amount">Amount</label>
            <p>{amount}</p>
          </div>
        </div>
        <button
          type="submit"
          className="mb-5 bg-blue-500 text-white font-bold py-2 px-8 rounded shadow border-2 border-blue-500 hover:bg-transparent hover:text-blue-500 transition-all duration-300"
        >
          {isEditing ? "Editing Row Item" : "Add Table Item"}
        </button>
      </form>

      {/* Table items */}

      <table width="100%" className="mb-10">
        <thead>
          <tr className="bg-gray-100 p-1">
            <td className="font-bold">Description</td>
            <td className="font-bold">hsn</td>
            <td className="font-bold">gst</td>
            <td className="font-bold">pcs</td>
            <td className="font-bold">gross wt</td>
            <td className="font-bold">net wt</td>
            <td className="font-bold">rate</td>
            <td className="font-bold">Amount</td>
          </tr>
        </thead>
        {list.map(({ id, description, hsn, gst, pcs, gweight, nweight, rate, amount }) => (
          <React.Fragment key={id}>
            <tbody>
              <tr className="h-10">
                <td>{description}</td>
                <td>{hsn}</td>
                <td>{gst}</td>
                <td>{pcs}</td>
                <td>{gweight}</td>
                <td>{nweight}</td>
                <td>{rate}</td>

                <td className="amount">{amount}</td>
                <td>
                  <button onClick={() => editRow(id)}>
                    <AiOutlineEdit className="text-green-500 font-bold text-xl" />
                  </button>
                </td>
                <td>
                  <button onClick={() => deleteRow(id)}>
                    <AiOutlineDelete className="text-red-500 font-bold text-xl" />
                  </button>
                </td>
              </tr>
            </tbody>
          </React.Fragment>
        ))}
      </table>

      <div>
        <h2 className="flex items-end justify-end text-gray-800 text-4xl font-bold">
          Rs. {total.toLocaleString()}
        </h2>
      </div>
    </>
  )
}
